<template>
    <div class="personal_zan_layer">
        <header class="login-header-bar clearfix zan-header-bar">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="login-title">赞</h2>
        </header>
        <section class="zan-box">
            <ul v-show="!showNoData">
                <li class="who-zan-item" v-for="(item,index) in likeDatas">
                    <div class="who-zan-top">
                        <div class="who-zan-names">
                            <img v-lazy="item.article.avatar" alt="" class="who-zan-pc">
                            <div class="who-zan-infos">
                                <p class="who-zan-name">
                                    {{item.article.author}}
                                </p>
                                <p class="who-zan-time">
                                    {{item.article.created_at | filterTime}}
                                </p>
                            </div>
                        </div>
                        <div class="zan-w clearfix">
                       <span class="zan-article">
                         <span v-for="(like,index) in likes">

                           <span v-show="likes.length<=2 && likes.length>0 && like.articleId === item.article.id">{{like.name}}</span>
                            <span v-show="likes.length>=3 && like.articleId === item.article.id">{{like.name}}</span>
                         </span>
                         <span v-show="allTotal[index].len<=2 && allTotal[index].len>0 && allTotal[index].id === item.article.id && item.article.article_type === 1">赞了这篇帖子</span>
                         <span v-show="allTotal[index].len>=3&& allTotal[index].id === item.article.id &&item.article.article_type === 1">等{{allTotal[index].len}}人赞了这篇帖子</span>
                         <span v-show="allTotal[index].len<=2 && allTotal[index].len>0 && allTotal[index].id === item.article.id && item.article.article_type === 0">赞了这篇新闻</span>
                         <span v-show="allTotal[index].len>=3&& allTotal[index].id === item.article.id &&item.article.article_type === 0">等{{allTotal[index].len}}人赞了这篇新闻</span>
                       </span>
                            <i class="zan-icon">

                            </i>
                        </div>
                    </div>
                    <div class="who-zan-bottom">
                      <router-link  class="who-zan-article" :to="'/tiezidetail/'+item.article.id" v-if="item.article.article_type === 1 || item.article.article_type === '1'">
                            <img v-lazy="item.article.image" alt="" class="article-pic">
                            <div class="article-content-box">
                                <h4 class="article-title">
                                    {{item.article.title}}
                                </h4>
                                <p class="article-zan-content" v-html="item.article.body">
                                    {{item.article.body}}
                                </p>
                            </div>
                      </router-link>
                      <router-link  class="who-zan-article" :to="'/newsdetail/'+item.article.id"  v-if="item.article.article_type === 0 || item.article.article_type === '0'">
                        <img v-lazy="item.article.image" alt="" class="article-pic">
                        <div class="article-content-box">
                          <h4 class="article-title">
                            {{item.article.title}}
                          </h4>
                          <p class="article-content">
                            {{item.article.body}}
                          </p>
                        </div>
                      </router-link>
                    </div>
                </li>

            </ul>

        </section>
      <NoData v-show="showNoData"></NoData>
    </div>
</template>
<script>
  import Api from '../../../api/api';
  import {getLikeDataInfo} from '../../../api/personalzan';
  import NoData from '../../nodata/NoData.vue';
  import logUtil from '../../../utils/log'
    export default{
        components:{
          NoData
        },
        data(){
          return{
              likeDatas:[],
              showNoData:false,
              articles:[],
              likes:[],
              allTotal:[],
          }
        },
        mounted(){
          this.getLikeData();
        },
      filters:{
        filterTime(value){
          return value.split("T")[0];
        }
      },
        methods:{
            goZanDetailPage($event){
                this.$router.push({path: '/newsdetail', replace: true});
                $event.preventDefault();
            },
            getLikeData(){
                const _this = this;
                getLikeDataInfo(`${Api.MYTIEZI}${localStorage.getItem('userId')}/likes/`).then((res)=>{
                              _this.initData(res);
                          },(err)=>{
                              _this.delErr();
                          });
            },
            initData(res){
              logUtil.printLog("获取到的点赞数据：",res);
                 if(res.status === 200 && res.data.data!==null){
                     this.likeDatas = res.data.data.detail;
                     const len = this.likeDatas.length;
                     len>0 ? this.showNoData = false:this.showNoData = true;
                     let tempArr = [];
                     for(let i=0;i<len;i++){
                         let likeLen = res.data.data.detail[i].liker.length;
                         let totalObj = {};
                         totalObj.len = likeLen;
                         totalObj.id = res.data.data.detail[i].article.id;
                         this.allTotal.push(totalObj);
                         if(likeLen>3){
                             likeLen = 3;
                         }
                         for(let j=0;j<likeLen;j++){
                             let like={

                             };
                             like.articleId = res.data.data.detail[i].article.id;
                             if(likeLen>=3){
                               like.name = res.data.data.detail[i].liker[j].personal_info.name+'、';
                             }else{
                               like.name = res.data.data.detail[i].liker[j].personal_info.name;
                             }
                             if(j>=2){
                               like.name=like.name.substring(0,like.name.length-1);
                             }
                              this.likes.push(like);
                         }
                     }
                 }else{
                   this.delErr();
                 }
            },
            delErr(){
              this.$store.dispatch('hideLoading');
              this.showNoData = true;
            }
        },

    }
</script>
<style scoped>
@import "../../../assets/css/pcenzan.css";
body {
    background-color: #EDEEF0;
}
</style>



